import React from 'react';
import './App.css';
import {FaBolt } from 'react-icons/fa';
import ReactAvatar from 'react-avatar';
import Box from '@material-ui/core/Box';

import {
  HashRouter as Router,
  Route,
} from 'react-router-dom'

import {db} from './firebase'
import ClassPage from './ClassPage';

var name
var colorOverlay

function Child ({match}) {
  name = match.params.id
  return(
    <div>
    </div>
  )
}

class ClassesDisplay extends React.Component{
  state = {
    classes: null,
    images: null,
    eligibility: null,
    qualifications: null,
    note: null,
    courses: null,
    offers: null
  }

  componentDidMount(){
    const data = db.collection('Classes').doc(name).collection('Information');    
     data.get()
      .then(snapshot=>{
        const classes = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          classes.push(data)
        })
        this.setState({classes:classes})
      })

    const images = db.collection('Classes').doc(name).collection('Images');    
      images.get()
      .then(snapshot=>{
        const images = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          images.push(data)
        })
        this.setState({images:images})
      })

      const eligibility = db.collection('Classes').doc(name).collection('Eligibility');    
      eligibility.get()
      .then(snapshot=>{
        const eligibility = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          eligibility.push(data)
        })
        this.setState({eligibility:eligibility})
      })

      const qualifications = db.collection('Classes').doc(name).collection('Qualifications');    
      qualifications.get()
      .then(snapshot=>{
        const qualifications = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          qualifications.push(data)
        })
        this.setState({qualifications:qualifications})
      })

      const note = db.collection('Classes').doc(name).collection('Note');    
      note.get()
      .then(snapshot=>{
        const note = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          note.push(data)
        })
        this.setState({note:note})
      })

      const courses = db.collection('Classes').doc(name).collection('Courses');    
      courses.get()
      .then(snapshot=>{
        const courses = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          courses.push(data)
        })
        this.setState({courses:courses})
      })

      const offers = db.collection('Classes').doc(name).collection('Offers');    
      offers.get()
      .then(snapshot=>{
        const offers = []
        snapshot.forEach(doc=>{
          const data = doc.data()
          offers.push(data)
        })
        this.setState({offers:offers})
      })
  }

  render() {
    return (
      <Router class='root'>
        {
          this.state.classes&&
          this.state.classes.map(classes=>{
            colorOverlay = classes.colorOverlay;
            return(
              <div>
                  <Router>
                    <div class="App">
                      <div class="wallpaper">
                        <img src={classes.wallpaper} alt="Avatar" class="image"/>
                        <div class="overlay" style={{ background: colorOverlay}}>
                          <div class="avatar">
                          <ReactAvatar src={classes.profileImage} round="50%" size="150" style={{background:"white"}}/>
                          </div>
                        </div>
                      </div>

                      <div class="details">
                      <Box boxShadow={2} bgcolor="transparent">
                          <table class="table">
                            <tr class="tr">
                              <td class="th">{classes.classes}/week</td>
                              <td class="th">â‚¹{classes.fees}/class</td>
                              <td class="th">{classes.experience}</td>
                            </tr>
                            <tr>
                              <td align="center">Classes</td>
                              <td align="center">Fees</td>
                              <td align="center">Experience</td>
                            </tr>
                          </table>
                      </Box>
                      </div>

                      <div class='carousel'>
                          {
                              this.state.offers&&
                              this.state.offers.map(offer=>{
                                return(
                                 <div class='offer' style={{borderColor:colorOverlay}}>
                                    <table style={{width:'170px',height:'auto'}}>
                                      <tr>
                                        <td><FaBolt color={colorOverlay} size='20'/></td>
                                        <th>{offer.title}</th>
                                      </tr>

                                      <tr>
                                        <td></td>
                                        <td>{offer.detail}</td>
                                      </tr>
                                    </table>
                                 </div>
                                )
                              })
                            }    
                      </div>
                
                      <div class="container">
                          <div class="head">General</div>
                          <div class="info">
                            <div class="field">Age</div>
                            <div class="attribute">{classes.age}</div>
                          </div>
                          <div class="info">
                            <div class="field">Type</div>
                            <div class="attribute">{classes.type}</div>
                          </div>
                          <div class="info">
                            <div class="field">Duration</div>
                            <div class="attribute">{classes.duration}</div>
                          </div>
                          <div class="info">
                            <div class="field">Timings</div>
                            <div class="attribute">{classes.timings}</div>
                          </div>
                          <div class="head">Highlights</div>
                          <div class="carousel">
                            {
                              this.state.images&&
                              this.state.images.map(image=>{
                                return(
                                  <img src={image.item}/>         
                    
                                )
                              })
                            }        
                          </div>
                          
                          <div class="head">Courses</div>
                          {
                              this.state.courses&&
                              this.state.courses.map(course=>{
                                
                                return(
                                  <table class="courseTable">
                                    <tr>
                                      <td style={{height:'50px',width:'50px'}} rowSpan='2'>
                                        <ReactAvatar src={course.image} round='50%' size='50'/>
                                         </td>

                                      <td style={{fontSize:'14px',height:'auto',paddingBottom:'0px'}}>{course.title}</td>
                                      <td style={{fontSize:'14px',paddingTop:'0px'}}>&#8377; {course.price}</td>
                                    </tr>
                                    
                                  </table>
                    
                                )
                              })
                           }    
                            

                          <div class="head">Eligibility</div>
                          <ul class="list">
                          {
                              this.state.eligibility&&
                              this.state.eligibility.map(eligibility=>{
                                return(
                                  <li>{eligibility.item}</li>
                                )
                              })
                            }    
                          </ul>

                          <div class="head">About Faculty</div>
                          <div class="listHeader">{classes.teacher}</div>
                          <ul class="list">
                          {
                              this.state.qualifications&&
                              this.state.qualifications.map(qualifications=>{
                                return(
                                  <li>{qualifications.item}</li>
                                )
                              })
                            }    
                          </ul>

                          <div class="head">Note from Teacher</div>
                          <ul class="list">
                          {
                              this.state.note&&
                              this.state.note.map(note=>{
                                return(
                                  <li>{note.item}</li>
                                )
                              })
                            }    
                          </ul>
                          <div style={{height:"70px", textAlign:'center',paddingTop:'20px'}}>
                             <p class="tail">©Pidgin2020</p>
                          </div>
                          
                        </div>
                
                    </div>
                  </Router>
              </div>
            )
          })
        }
        <Route path="/:id" component={Child}/>
        <Route path="/classes" component={ClassPage}/>
      </Router>
    )
  }
}
export default ClassesDisplay;
